.hero {
  position: relative;
  margin-bottom: 37px;
  padding-top: 52px;
  width: @mobile-width;
  min-height: 158px;
  background-image: url("../img/hero-background.jpg");
  background-size: @mobile-width 214px;
  background-repeat: no-repeat;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    width: 600px;
    height: 57px;
    background-image: url("../img/decoration.svg");
    background-repeat: no-repeat;
    background-size: 600px 57px;

    @media (max-width: @tablet-width) {
      right: calc(@mobile-width / 2);
    }

    @media (min-width: @tablet-width) {
      right: calc(@tablet-width / 2);
    }

    @media (min-width: @desktop-width) {
      right: calc(@desktop-width / 2);
    }
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    width: 600px;
    height: 57px;
    background-image: url("../img/decoration.svg");
    background-repeat: no-repeat;
    background-size: 600px 57px;
    transform: scaleX(-1);

    @media (max-width: @tablet-width) {
      left: calc(@mobile-width / 2);
    }

    @media (min-width: @tablet-width) {
      left: calc(@tablet-width / 2);
    }

    @media (min-width: @desktop-width) {
      left: calc(@desktop-width / 2);
    }
  }

  @media (min-width: @tablet-width) {
    width: @tablet-width;
    min-height: 291px;
    margin-bottom: 66px;
    padding-top: 90px;
    background-size: @tablet-width 298px;
  }

  @media (min-width: @desktop-width) {
    width: @desktop-width;
    min-height: 296px;
    margin-bottom: 100px;
    padding-top: 101px;
    background-size: @desktop-width 298px;
  }
}

.hero__image {
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    width: 457px;
    height: 78px;
    margin: 0 auto;
  }
}
